body {
  margin: 0;
  background-color:  white;
  /* overflow: hidden; */
  font-family: 'roboto','proxima-nova';
}

* {
box-sizing: border-box;
}cd 

.dummy {
  grid-area: dummy;
  background:  rgb(11, 55, 68);
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
}

.top {
  grid-area: top;
  background:  rgb(11, 55, 68);
  grid-column-start: 1;
  grid-column-end: 5;
  grid-row-start: 2;
  grid-row-end: 3;
  display: grid;
  grid-template-columns: 54.5% 45.5%;
}

.Main {
  display: grid;
  grid-template-columns: 10% auto 7% 13%;
  grid-template-rows: 5px 35px auto 50px;
  background: rgb(88, 84, 84);
  color:white;
  height: 100vh;
}
