.charts_container{
    grid-row: 3/5;
    grid-column: 2/3;

    display: grid;
    grid-template-rows: 50% 50%;
}

.textbox_container{
    grid-row: 3/5;
    grid-column: 4/5;
    color:yellowgreen;
    background: rgb(12, 7, 46);
    display: grid;
    grid-template-rows : repeat(6,1fr);
    grid-gap: 5px;
}

.textbox{
   display: grid;
   grid-template-rows: 40% 60%;
   border: 2px solid rgb(82, 88, 70);
   background: rgb(20, 35, 85);
}
.textbox h2,h3,h4{
    margin:0 0;
}

.text_values{
    display: grid;
    grid-template-columns: 70% 30%;
}
.chart1{
    grid-column: 1/2;
    grid-row: 1/2;
}
.chart2{
    grid-column: 1/2;
    grid-row: 2/3;
}

